@use "../abstracts";
.ant-menu-item {
  margin-top: 20px !important;
  padding-left: 24px !important;
  padding-top: 5px !important;
  padding-bottom: 3px !important;
}
.ant-menu-item-selected {
  background: #b8b8bb;
  background: var(--background-hover-navbar);
}

//slider
.slider-container {
  border-radius: 10px;
  height: 335px;
  width: 100%; // iu chnh kch thc theo nhu cu
  overflow: hidden; // n phn trn ra ngoi  slider gn gng
}

.slick-slide {
  display: flex; // m bo cc slide c hin th nh cc phn t flex
  justify-content: center; // Cn gia ni dung bn trong cc slide
  align-items: center; // Cn gia ni dung theo chiu dc bn trong cc slide
}
.slick-dots {
  display: flex !important;
  gap: 6px;
}
.slick-dots li,
.slick-dots li button {
  width: 32px;
  border-radius: 8px;
  height: 4px;
  background: var(--background-color-main) !important;
  position: relative;
  @include abstracts.breakpoint(md) {
    width: 20px !important;
  }
}

.slick-dots li.slick-active button {
  background: var(--background-color-main-2) !important;
  z-index: 2;
  width: 32px !important;
  height: 4px !important;
  border-radius: 8px !important;
  position: relative;
  @include abstracts.breakpoint(md) {
    width: 20px !important;
  }
}
.slick-dots li button:before {
  display: none;
}
.ant-layout {
  background: var(--color-background-layout) !important;
}
.ant-layout-content {
  min-height: auto !important;
}
.ant-menu-item-selected {
  background: var(--background-active-color) !important;
}
